<template>
    <div id="analytics">
        <h1><b></b></h1>

        <div class="clearfix">
            
            <!-- Line Chart -->
            <div class="box">
                <h3>- </h3>
                <h5 class="y-axis">-<br>Sweet Computer</h5>
                    <div class="size-line-chart">
                        <zingchart></zingchart>
                    </div>
                <h5 class="x-axis"></h5>

                <!-- describes -->
                <!-- <div class="describe" style="text-align:left;">
                    <b>:</b>   player +1
                    <br>  the sweet learning computer  +1
                </div> -->
                
                <div>: <span id="meanPlayTime">0</span> </div>
                <div>: <span id="playRound">0</span> </div>
            </div>
            
            <!-- Score pie-chart -->
            <div class="box">
                <h3></h3>
                
                <div style="padding-top: 100px"> 
                    <div class="clearfix">
                        <div class="float">
                            <canvas id="my-chart1"></canvas>
                            <div style="margin-top: 20px">: <span id="lawRate">0</span> %</div>
                        </div>

                        <div class="float">
                            <canvas id="my-chart2"></canvas>
                            <div style="margin-top: 20px">: <span id="learningRate">0</span> %</div>
                        </div>

                    </div>
                    <!-- describes -->
                    <div class="describe" style="margin-top: 20px">
                        <div>: <span id="timeRate">0</span> </div> 
                        <div>: <span id="roundRate">0</span> </div>   
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Vue from 'vue';
import AppVue from './App.vue';
import zingchart from "./linechart.vue";
import Chart from 'chart.js';


export default {
    name:"analytics",
    data: function(){
        return { 
            score_1: 0,
            score_2: 0,
            minute: 0,
            second: 0
        }
    },
    components: {
        zingchart: zingchart
    },
    mounted(){
        this.getTestScore();
        if(localStorage.ArraySecondsOfPlay != null){
            this.getPlayTimeNRound();
        }
    },
    methods:{
        getTestScore() {
            var score1 = 1;
            var score2 = 1;
            if(localStorage.sumLawScore != 0 && localStorage.sumLearningScore != 0){
                score1 = localStorage.sumLawScore;
                score2 = localStorage.sumLearningScore;
            }
            
                
            /* Draw a chart 1 */
            var ctx = document.getElementById('my-chart1');
            var myChart = new Chart( ctx, {
                type: 'pie',
                data: {
                    labels: ['', ''],
                    datasets: [{
                    label: 'Page A',
                    data: [ score1, (localStorage.timesTest*3)-score1 ],
                    backgroundColor: ['#018abe', '#d6e8ee']
                   }]
                },
                options: {
                    responsive: true
                }
            })
            /* Draw a chart 1 */

            /* Draw a chart 2 */
            var ctx = document.getElementById('my-chart2');
            var myChart = new Chart( ctx, {
                type: 'pie',
                data: {
                    labels: ['', ''],
                    datasets: [{
                    label: 'Page A',
                    data: [ score2 , (localStorage.timesTest*2)-score2 ],
                    backgroundColor: ['#018abe', '#d6e8ee']
                   }]
                },
                options: {
                    responsive: true
                }
            })
            /* Draw a chart 2 */

            // var Arrayseconds = localStorage.getItem('ArraySecondsOfTest');
            // var array = Arrayseconds.split(",");
            if(localStorage.sumLawScore+localStorage.sumLearningScore > 0){
                this.showTestScore();
            }
            
        },
        showTestScore(){
            /*  law_score, learning_score, TestMeanTime ,TestRound*/
            var Arrayseconds = localStorage.getItem('ArraySecondsOfTest');
            var array = Arrayseconds.split(",");

            var sumseconds = 0;
            for(var i=1 ; i<array.length ; i++){
                sumseconds += parseInt(array[i]);
            }
            var testRate = sumseconds/localStorage.timesTest;
            AppVue.minute = testRate / 60;
            AppVue.second = testRate % 60;
            AppVue.minute = AppVue.minute < 10 ? "0" + AppVue.minute.toFixed(0) : AppVue.minute.toFixed(0);
            AppVue.second = AppVue.second < 10 ? "0" + AppVue.second.toFixed(0) : AppVue.second.toFixed(0);
            document.getElementById("timeRate").innerText = AppVue.minute +"."+AppVue.second;

            document.getElementById("lawRate").innerText = ( (localStorage.sumLawScore/(localStorage.timesTest*5))*100 ).toFixed(2);
            document.getElementById("learningRate").innerText = ( (localStorage.sumLearningScore/(localStorage.timesTest*5))*100 ).toFixed(2);
            document.getElementById("roundRate").innerText = localStorage.timesTest;
            /*  law_score, learning_score, MeanTime */     
        },
        getPlayTimeNRound(){

                /*  PlayMeanTime, PlayRound */
                var Arrayseconds = localStorage.getItem('ArraySecondsOfPlay')
                var array = Arrayseconds.split(",");
                var sumseconds = 0;
                for(var i=1 ; i<array.length ; i++){
                    sumseconds += parseInt(array[i]);
                }
                var meanPlayTime = sumseconds/localStorage.times;
                AppVue.minute =  meanPlayTime / 60;
                AppVue.second =  meanPlayTime % 60;
                AppVue.minute = AppVue.minute < 10 ? "0" + AppVue.minute.toFixed(0) : AppVue.minute.toFixed(0);
                AppVue.second = AppVue.second < 10 ? "0" + AppVue.second.toFixed(0) : AppVue.second.toFixed(0);
                document.getElementById("meanPlayTime").innerText = AppVue.minute+"."+AppVue.second;
                document.getElementById("playRound").innerText = localStorage.times;
                /*  PlayMeanTime, PlayRound */

        }
    }
}
</script>

<style scoped>

h1, .clearfix {
    font-family: 'Bai Jamjuree', sans-serif;
    font-weight: 300;
}
.descript{
    text-align: left;
}
.x-axis{
    text-align: right;
    padding: 0px;
    margin: 0px;
}
.x-axis2{
    float: left;
    padding: 0px;
    padding-left: 45px;
    margin: 0px;
}
.y-axis{
    text-align: left;
    padding: 0px;
    margin: 0px;
}
.box {
    border: 1px solid lightgray ;
    float: left;
    margin: 20px;
    width: 45%;
    padding: 15px;
    
  /* margin: 1%; */
}
.float {
    float: left;
    margin: auto;
    width: 50%;
    padding: 0px;
}
.clearfix::after {
  content: "";
  clear: both;
  display: table;
}
.grid {
  display: grid;
  row-gap: 2rem;
}
.size-line-chart{
    width: 100%;
}
</style>